  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (this.Chartinfo != undefined) {
      this.Chartinfo.resize();
    }
    if (this.ChartMain != undefined) {
      this.ChartMain.resize();
    }
  }

  return() {
    this.disable_buy = false;
    this.disable_sell = false;
    this.show = false;
    this.selectcrypto = null;
  }

  buysell(action: string) {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    dialogConfig.maxWidth= '100vw';
    dialogConfig.width = "auto";

    const data: any = {
      logo: this.selectcrypto.logo,
      symbol: this.selectcrypto.symbol,
      price: this.selectcrypto.price,
      action: action
    }
    this.manage_data.emitEvent(EnumEventsPopUp.buy_sell.toString(), data);

    this.dialog.open(BuySellComponent,dialogConfig);
  }


  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  async ngOnInit() {
    this.spinner.show();
    await this.GetDataBloxOrder();
    await this.SetPortfolioData();
    await this.changeTimeMain("1h");
    await this.GetEvent();
    this.spinner.hide();
    

  }
  async GetEvent() {
    this.subscription.add(this.manage_data.getSubject(EnumEventsPopUp.ChangeColor.toString()).subscribe(data => {
      if (data != null) {

        if (!this.show) {
          this.changeTimeMain(this.datainicial);

        } else {
          this.changeTimeInfo(this.datainicial);
        }

      }
    }))
  }

  async ReloadPortfolioData() {
    var products = this.products;
    let portafoliDeepInfoResponse = await this.GetDeepPortfolioInfo();
    this.portfolio_balances = portafoliDeepInfoResponse.holdings;
    this.portfolio_balances.forEach(element => {
      let data = products.find(x => x.base_currency === element.base_currency);
      if (data) {
        if (data.qty != element.qty) {
          if(this.selectcrypto != null && this.selectcrypto.symbol == element.base_currency){
            if(element.qty <= 0){
              this.disable_sell = true;
            }else{
              this.disable_sell = false;
            }
            this.selectcrypto.qty = element.qty;
          }else if(element.base_currency == 'USD'){
            data.qty = element.qty;
            if(data.qty <= 0){
              this.disable_buy = true;
            }else{
              this.disable_buy = false;
            }
          }
          
          this.SetList();
        }
      } else {
        this.SetPortfolioData();
      }
    });
  }

