import { Injectable } from '@angular/core';
import * as echarts from 'echarts';

@Injectable({
    providedIn: 'root'
})
export class GraphInfoCryptoService {

    constructor() { }


    public makeOption() {
        var languageCountryCode;

        return {
            animation: true,
            grid: {
                left: '0%',
                right: '0%', //'8%', //'12%',
                bottom: 75,  // TODO: dynamically adjust based on device type
                top: 5,
            },

            xAxis: [
                {
                    type: 'time',
                    min: 'dataMin',
                    max: 'dataMax',
                    splitNumber: 2,
                    interval: 2,
                    scale: true,
                    axisPointer: {
                        snap: true,
                        lineStyle: {
                            color: '#7581BD',
                            width: 1
                        },
                        label: {
                            show: false,
                            formatter: function (params) {
                                return echarts.format.formatTime('MM-dd hh:mm', params.value);
                            },
                            backgroundColor: '#7581BD'
                        },
                    },
                    splitLine: {
                        show: false
                    },
                    axisLine: {
                        onZero: false,
                        show: true,
                        lineStyle: {
                            color: '#FFFFFF'
                        }
                    },
                    axisLabel: {
                        formatter: function (value) {
                            return echarts.format.formatTime('MM-dd hh:mm', value);
                        },
                        textStyle: {
                            fontSize: 12,
                            color:"",
                        },
                        margin: 12,
                    },
                },
            ],
            yAxis: [
                {
                    show: true,
                    scale: true,
                    type: 'value',
                    axisTick: {
                        show: false
                    },
                    splitLine: {
                        show: false,
                        lineStyle: {
                            type: 'dotted',
                            opacity: 0.75,
                        },
                    },
                    zlevel: 1000,
                    position: 'left',
                    axisLabel: {
                        textStyle: {
                            fontSize: 12,
                            color:"",

                        },
                        inside: true,
                        verticalAlign: 'middle',
                        margin: 0,
                        showMinLabel: false,
                        showMaxLabel: false,
                        formatter: function (value) {
                            const options = {};
                            const numberFormat = new Intl.NumberFormat(languageCountryCode, options);
                            var formattedValue = numberFormat.format(value);
                            return formattedValue;
                        },
                    },
                    min: function (value) {
                        return value.min;
                    },
                    max: function (value) {
                        return value.max;
                    },
                },
                {
                    show: true,
                    type: 'value',
                    scale: true,
                    splitLine: {
                        show: false,
                        lineStyle: {
                            type: 'dotted',
                            opacity: 0.75,
                        },
                    },
                    axisTick: {
                        show: false
                    },
                    zlevel: 1000,
                    position: 'right',
                    axisLabel: {
                        textStyle: {
                            fontSize: 12
                        },
                        inside: false,
                        verticalAlign: 'middle',
                        align: 'right',
                        margin: 0, //24,
                        showMinLabel: false,
                        showMaxLabel: false,
                        formatter: function (value) {
                            const options = {};
                            const numberFormat = new Intl.NumberFormat(languageCountryCode, options);
                            var formattedValue = numberFormat.format(value);
                            return formattedValue;
                        },
                    },
                    min: function (value) {
                        var multFactor = 1 / 1.026;
                        var min = value.min;

                        if (min < 0) {
                            if (value.min < 0) {
                                multFactor = 1 / multFactor;
                            }
                            min = min * multFactor;
                            var newMin = Math.round((min + Number.EPSILON) * 100) / 100;
                            return newMin;
                        } else {
                            return 0;
                        }
                    },
                    max: function (value) {
                        //var multFactor = 1.10;
                        var multFactor = 1.08;
                        if (value.max < 0) {
                            multFactor = 1 / multFactor;
                        }
                        var max = value.max * multFactor;
                        var newMax = Math.round((max + Number.EPSILON) * 100) / 100;
                        return newMax;
                    }
                },

            ],
            visualMap:
                [
                    {
                        show: false,
                        dimension: 1,
                        seriesIndex: 0,
                        pieces: [
                            {
                                lt: 0,
                                color: 'red'
                            },
                            {
                                gte: 1,
                                lte: 1000000,
                                color: 'green'
                            }
                        ]
                    },
                    {
                        show: false,
                        dimension: 1,
                        seriesIndex: 1,
                        pieces: [
                            {
                                lt: 0,
                                color: 'rgb(255, 0, 0)',
                            },
                            {
                                gte: 1,
                                lte: 1000000,
                                color: 'rgb(0, 100, 0)',
                            }
                        ]
                    },
                ],
            series: [
                {
                    // 0
                    type: 'line',
                    yAxisIndex: 1,
                    smooth: true,
                    name: 'Unrealized P/L',
                    symbolSize: 6,
                    // symbol: 'none',
                    data: [],
                    axisLabel: {
                        textStyle: {
                            fontSize: 12
                        },
                    },
                    lineStyle: {
                        //color: 'rgba(139, 128, 0, 0.5)',
                        width: 1,
                    },
                    itemStyle: {
                        //color: 'rgba(139, 128, 0, 1.0)',
                    },
                    markPoint: {}
                },
                {
                    // 1
                    type: 'bar',
                    z: 2,
                    yAxisIndex: 1,
                    barWidth: 8,
                    name: 'Realized P/L',
                    data: [],
                    axisLabel: {
                        textStyle: {
                            fontSize: 12
                        },
                    },
                    itemStyle: {
                    },
                    markPoint: {},
                },
                {
                    // 2
                    type: 'line',
                    smooth: true,
                    name: 'Asset Price',
                    yAxisIndex: 0,
                    symbolSize: 6,
                    z: 1,
                    //symbol: 'none',
                    data: [],
                    axisLabel: {
                        textStyle: {
                            fontSize: 12
                        },
                    },
                    itemStyle: { color: 'rgb(0, 0, 255)' },
                    lineStyle: {
                        color: 'rgb(0, 0, 255)',
                        width: 1,
                    },
                    markPoint: {}
                },
                {
                    // 3
                    type: 'scatter',
                    yAxisIndex: 0,
                    smooth: true,
                    name: 'Trade Buy Price',
                    symbol: 'diamond',
                    symbolSize: 10,
                    z: 10,
                    data: [],
                    axisLabel: {
                        textStyle: {
                            fontSize: 12
                        },
                    },
                    itemStyle: { color: 'rgb(0, 255, 0)' },
                    markPoint: {},
                },
                {
                    // 4
                    type: 'scatter',
                    yAxisIndex: 0,
                    smooth: true,
                    name: 'Trade Sell Price',
                    symbol: 'diamond',
                    symbolSize: 10,
                    z: 10,
                    data: [],
                    axisLabel: {
                        textStyle: {
                            fontSize: 12
                        },
                    },
                    itemStyle: { color: 'rgb(255, 0, 0)' },
                    markPoint: {},
                },

            ],
            backgroundColor: 'rgb(0, 0, 0)',
            legend: {
                itemWidth: 18 * 1.5,
                itemHeight: 10 * 1.5,
                show: true,
                height: 120,
                itemGap: 10,
                left: 'auto',
                right: 'auto',
                top: 'auto',
                bottom: -5,      // TODO: dynamically adjust based on device type
                //padding: 8,
                padding: [
                    5,  // up
                    0, // right
                    5,  // down
                    0, // left
                ],
                inactiveColor: 'rgba(100, 100, 100, 0.50)',
                data: [
                    { name: 'Unrealized P/L', icon: 'image://assets/Graph/unrealizedPnL.png' },
                    { name: 'Realized P/L', icon: 'image://assets/Graph/realPAndL.png' },
                    { name: 'Asset Price', icon: 'image://assets/Graph/assetPrice.png' },
                    { name: 'Trade Buy Price', icon: 'image://assets/Graph/buyTradePrice.png' },
                    { name: 'Trade Sell Price', icon: 'image://assets/Graph/sellTradePrice.png' },
                ],
            },

            tooltip: {
                trigger: "axis",
                axisPointer: {
                    type: "line",
                },
                borderColor: 'rgba(0, 0, 0, 1.0)',
                backgroundColor: 'rgba(32, 32, 32, 0.85)',
                textStyle: {
                    color: 'rgba(255, 255, 255, 1.0)',
                    fontSize: 10,
                },
                formatter: function (param) {
                    return param;
                },
                position: function (pos, params, el, elRect, size) {
                    var obj = { top: 10 };
                    obj[['left', 'right'][+(pos[0] < size.viewSize[0] / 2)]] = 0;
                    return obj;
                },
                extraCssText: 'width: 12rem'
            },

        };
    }
};



