import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { ConfirmationComponent } from 'src/app/PopUp/confirmation/confirmation.component';
import { QrReaderComponent } from 'src/app/PopUp/qr-reader/qr-reader.component';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';
import { PortafolioDefault } from 'src/models/Portafolio/portfolioDefault.model';
import { ClientService } from 'src/services/Client/client.service';
import { ManageDataService } from 'src/services/manage-data.service';
import { PaymentService } from 'src/services/payments/payment.service';
import { PortafolioService } from 'src/services/portafolio/portafolio.service';
import { TestFoundsService } from 'src/services/TestFounds/test-founds.service';
import { WalletService } from 'src/services/Wallet/wallet.service';
import { SocketioService } from 'src/services/WebSocket/socketio.service';
import { confirm } from 'src/models/Confirm/confirm.model';
import { EnumConfirmation } from 'src/models/Enum/EnumAuthentication.enum';
import { clientPayMethod } from 'src/models/PaymentMethods/clientPayMethods.model';
import { BalanceModel, BalanceModelResponse } from 'src/models/Portafolio/portfolioBalance.model';
import { BalanceForSymbolModel, BalanceForSymbolModelResponse } from 'src/models/Portafolio/portfolioBalanceForSymbol.model';
import { GetGasFeeEstimateModel, GetGasFeeEstimateModelResponse } from 'src/models/Wallet/wallet.model';
import { WithdrawMaxModel, WithdrawModel, WithdrawModelResponse } from 'src/models/TestFounds/testFounds.model';
import { PaymentMethodsComponent } from 'src/app/PaymentMethods/payment-methods/payment-methods.component';
import { ClientConfigResponse } from 'src/models/Client/ClientConfig.model';
import { ConvertDataModelResponse, ConvertModel } from 'src/models/Market/market.model';
import { MarketService } from 'src/services/Market/market.service';


@Component({
  selector: 'app-withdraw',
  templateUrl: './withdraw.component.html',
  styleUrls: ['./withdraw.component.scss']
})
export class WithdrawComponent implements OnInit, OnDestroy {

  language: string;
  langs: string[] = [];

  assets_data = [];
  withdraw_methods:clientPayMethod[] = [];
  products_id = [];
  assets_values_socket = [];

  usd_select:any = false;
  crypto_select:any = false;
  payment_method_select:any = false;
  withdraw:any = true;
  final_dialog:any = false;
  button_disabled:any = false;
  fee_select:any = false;
  crypto_valid_value:any = false
  label_show:any = false;
  isChecked:any = false;
  button_address:any = false;
  validating:any = false;

  payment_method_name:any;
  crypto_qty:any;
  msg_events: any;
  crypto_name:any;
  input_amount_value:any = "";
  portfolio_name:any;
  total_qty_crypto:any;
  fee_data:any;
  address_data:any = '';

  client_data:any;
  input_currency_value:any = 0;
  input_usd_value:any = 0;
  date: Date = new Date();
  portfolioBalances:any;
  loadCompleteOK:boolean;
  
  withdrawMAX:boolean;
  disable_withdraw: boolean = false;

  timerConversionCurrency:any;
  timerConversionUsd:any;



  public subscription: Subscription = new Subscription();
  minutes: string;
  cost: string;
  fee_value: string;

  constructor(private wallet_service:WalletService , private websocket:SocketioService, 
    private toastr:ToastrService, private test_payments:TestFoundsService, 
    private client:ClientService, private payments:PaymentService, private market:MarketService,
    private assets:PortafolioService, private translate: TranslateService, 
    private router:Router, private dialog: MatDialog,private manage_data: ManageDataService<any>,
    private spinner:NgxSpinnerService
    ) {
   }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  
  async ngOnInit() {
    this.spinner.show();
    await this.InitWithdraw();
    await this.IsValidatedID();
    this.spinner.hide();

  }

  async ReadQR(){
    this.address_data = '';
    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "25%";
    //dialogConfig.width = "40rem";
    let dialogRef = this.dialog.open(QrReaderComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(() => {
      this.subscription.add(this.manage_data.getSubject(EnumEventsPopUp.QRReaderWithdraw.toString()).subscribe(response=>{
        this.address_data = response;
        this.button_address = true;
      }))
    }));

    
  }
  
  async ConfirmAddMethod(): Promise<void> {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.disableClose = true;
    dialogConfig.width = "40rem";
    var data = {
      title: await this.translate.get('dialog.generateWithdraw').toPromise(),
      icon: 'quest',
      conten: await this.translate.get('dialog.agreewithmethod').toPromise()
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(), data);
    let dialogRef = this.dialog.open(ConfirmationComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.WithdrawNotSaved();
      }

    }));
  }
  

  //************************************************************************************
  //Confirm Validate ID
  //************************************************************************************


  async ConfirmValidateID(): Promise<void> {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "20rem";
    dialogConfig.width = "40rem";

    var data = {
      title: await this.translate.get("Errores.mustValidateID").toPromise(),
      icon: 'quest',
      conten: await this.translate.get("Errores.wantValidateID").toPromise()
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(), data);
    let dialogRef = this.dialog.open(ConfirmationComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.router.navigate(['/vouched']);
      }

    }));
  }

  //************************************************************************************
  //Is Validated ID?
  //************************************************************************************

  async IsValidatedID(){
    let data = await this.GetClientConfig()
    if(data.id_verified){
      this.disable_withdraw = false;
    }else{
      this.disable_withdraw = true;
      this.ConfirmValidateID();
    }
  }

  //************************************************************************************
  //Init Withdraw
  //************************************************************************************


  async InitWithdraw(){
    try {

      //===========================================================================
      //Get client config
      //===========================================================================
      let getClientConfigResponse = await this.GetClientConfig();
      if (!getClientConfigResponse) {
        return;
      }
      this.client_data = getClientConfigResponse;
      // console.log("getClientConfigResponse:", getClientConfigResponse);

      //===========================================================================
      //Get Default portafolio
      //===========================================================================
      let getDefaultPortafolioResponse = await this.GetDefaultPortafolio();
      if (!getDefaultPortafolioResponse) {
        return;
      }

      // console.log("getDefaultPortafolioResponse:", getDefaultPortafolioResponse);
      this.portfolio_name = getDefaultPortafolioResponse.name;

      //===========================================================================
      //Get Default portafolio
      //===========================================================================
      let portfolioBalancesResponse = await this.GetPortfolioBalances();
      if (!portfolioBalancesResponse) {
        return;
      }

      // console.log("portfolioBalancesResponse:", portfolioBalancesResponse);
      if(portfolioBalancesResponse.length === 0){
        return;
      }

      portfolioBalancesResponse = portfolioBalancesResponse.filter(x=>x.qty > 0);
      // console.log("portfolioBalancesResponse after:", portfolioBalancesResponse);
      if(portfolioBalancesResponse.length === 0){
        this.ShowWarning("You have no money or coins to withdraw");
        return;
      }


      this.portfolioBalances = portfolioBalancesResponse.sort((a, b) => {
        if (a.fiat) return -1;
        if (a.base_currency > b.base_currency) return 1;
        else return -1;
      });;

      // console.log("portfolioBalancesResponse sort:", portfolioBalancesResponse);
      this.products_id = portfolioBalancesResponse.filter(y=>y.fiat===false).map(x => `${x.base_currency}-USD`);
      // console.log("products_id:", this.products_id);

      //===========================================================================
      //Init Blox socket
      //===========================================================================
      this.websocket.InitBlox(this.products_id);

      //===========================================================================
      //Get all withdraw methods
      //===========================================================================
      let getAllPaymentMethodsResponse = await this.GetAllPaymentMethods();
      if (!getAllPaymentMethodsResponse) {
        this.ShowWarning("Please add a withdraw method to the portfolio", "There are no withdraw methods", false);
      }

      // console.log("getAllPaymentMethodsResponse:", getAllPaymentMethodsResponse);
      this.withdraw_methods = getAllPaymentMethodsResponse;

      this.loadCompleteOK = true;


  
      //===========================================================================
      //WebSocket Coins Blox
      this.subscription.add(this.websocket.GetDataBlox().subscribe(data=>{
        if(typeof data != 'object' && data != "Hey Dude!"){
          this.SetValues(JSON.parse(data))
        }
      }))

      //===========================================================================
      
    } catch (error) {
      console.error("Error on InitDeposit:", error);
    }
  }

  SetValues(data:any){

    const data_value:any = {
      product_id: data['product_id'],
      price: data['mid_price']
    }
    
    let asset = this.assets_values_socket.find(element => element['product_id'] == data_value['product_id']);
    
    if(asset){
      if(asset.price != data_value.price){
        asset.price = data_value.price;
      }
    }else{
      this.assets_values_socket.push(data_value);
    }
    //console.log(this.assets_data);
  }

  

  async SelectCurrency(value:any){
    if (this.loadCompleteOK === false) {
      var msg = await this.translate.get('Errores.pleaseReload').toPromise()
      this.ShowError(msg, "Error");
      return;
    }

    switch (value) {
      case 'USD':
        this.crypto_select = false;
        this.usd_select = true;
        this.crypto_valid_value = false;
        this.label_show = false;
        this.input_amount_value = 0;
        if(this.withdraw_methods.toString() === ""){
          this.ConfirmAddMethod();
          break;
        }
        break;
      
      case 'Select':
      
        break;
    
      default:
        this.crypto_name = value;

        const balanceSymbol:BalanceForSymbolModel = {
          portfolio_name: this.portfolio_name,
          symbol: this.crypto_name
        }

        const coin:GetGasFeeEstimateModel = {
          coin: this.crypto_name
        }
        
        let balanceForSymbolResponse = await this.GetBalanceForSymbol(balanceSymbol);
        if (!balanceForSymbolResponse) {
          return;
        }
        // console.log("balanceForSymbolResponse:", balanceForSymbolResponse)

        this.total_qty_crypto = balanceForSymbolResponse.qty;

        let estimatedFeeResponse = await this.GetEstimatedFee(coin);
        if (!estimatedFeeResponse) {
          return;
        }
        // console.log("estimatedFeeResponse:", estimatedFeeResponse)

        this.fee_data = estimatedFeeResponse;



        this.crypto_valid_value = false;
        this.label_show = false;
        this.usd_select = false;
        this.button_address = false;
        this.crypto_select = true;
        this.payment_method_select = false;
        
        
        
       
      break;
    }
  }

  SelectMethod(value:any){
    switch (value) {
      
      case 'Select':
        this.payment_method_select = false;
        break;
    
      default:
        this.payment_method_select = true;
        this.payment_method_name = value;
        break;
    }
  }

  ContinuePaymentButton(){
    this.usd_select = false;
    this.button_address = false;
    this.crypto_select = false;
    this.payment_method_select = false;
    this.withdraw = false;
    this.final_dialog = true;
  }

  PerceintsButtons(perceint:any){

    switch (perceint) {
      case '25':
        this.withdrawMAX = false;
        this.input_amount_value = Number(this.total_qty_crypto) * 0.25;
        break;

      case '50':
        this.withdrawMAX = false;
        this.input_amount_value = Number(this.total_qty_crypto) * 0.5;
        break;

      case '75':
        this.withdrawMAX = false;
        this.input_amount_value = Number(this.total_qty_crypto) * 0.75;
        break;

      case '100':
        this.withdrawMAX = true;
        this.input_amount_value = Number(this.total_qty_crypto);
        break;

      default:
        break;
    }
    if(this.input_amount_value != 0){
      this.crypto_valid_value = true;
    }else{
      this.crypto_valid_value = false;
    }
  }

  ActionFee(data:any){

    let price = this.assets_values_socket.find(element=> element.product_id == this.crypto_name+'-USD')

    switch (data) {

      case 'urgent':
        this.fee_select = true;
        this.minutes = Number(this.fee_data[data]['time_in_secs'])/60+ " ";
        this.cost = Number(this.fee_data[data]['cost']).toFixed(8)+" "+this.crypto_name; 
        this.fee_value = " ($"+(this.fee_data[data]['cost'] * price.price).toFixed(2)+")";
        this.label_show = true;
        
        break;
      
      case 'fast':
        this.fee_select = true;
        this.minutes = Number(this.fee_data[data]['time_in_secs'])/60+ " ";
        this.cost = Number(this.fee_data[data]['cost']).toFixed(8)+" "+this.crypto_name; 
        this.fee_value = " ($"+(this.fee_data[data]['cost'] * price.price).toFixed(2)+")"; 
        this.label_show = true;
        
        break;
      
      case 'normal':
        this.fee_select = true;
        this.minutes = Number(this.fee_data[data]['time_in_secs'])/60+ " ";
        this.cost = Number(this.fee_data[data]['cost']).toFixed(8)+" "+this.crypto_name; 
        this.fee_value = " ($"+(this.fee_data[data]['cost'] * price.price).toFixed(2)+")";
        this.label_show = true;

        break;
      
      case 'fee_saver':
        this.fee_select = true;
        this.minutes = Number(this.fee_data[data]['time_in_secs'])/60+ " ";
        this.cost = Number(this.fee_data[data]['cost']).toFixed(8)+" "+this.crypto_name; 
        this.fee_value = " ($"+(this.fee_data[data]['cost'] * price.price).toFixed(2)+")";
        this.label_show = true;

        break;
    
      default:
        break;
    }
  }

  //****************************************************
  //Withdraw Without Payment Method Saved
  //****************************************************

  WithdrawNotSaved(){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "100rem";
    const data = {
      action: 'Withdraw',
      direction: null
    }
    this.manage_data.emitEvent(EnumEventsPopUp.ActionPaymentMethods.toString(),data)
    let dialog = this.dialog.open(PaymentMethodsComponent,dialogConfig);
    this.subscription.add(dialog.afterClosed().subscribe(async ()=>{
      let getAllPaymentMethodsResponse = await this.GetAllPaymentMethods();
      if (!getAllPaymentMethodsResponse) {
        this.ShowWarning("Please add a withdraw method to the portfolio", "There are no withdraw methods", false);
      }
      this.withdraw_methods = getAllPaymentMethodsResponse;
    }))
  }

  //****************************************************
  //Payment Process
  //****************************************************


  PaymentProccessCrypto(){
    this.validating = true;
    this.button_disabled = true;
  
    const fundsOut:WithdrawModel = {
      portfolio_name: this.portfolio_name,
      withdraw_to: "My Wallet at another custodian",
      instrument: this.crypto_name,
      qty: this.input_amount_value,
      notes: "Wired from Chase"
    }
    if(this.withdrawMAX){
      console.log("Withdraw MAX");
      delete fundsOut.qty;
      console.log("Withdraw Data ",fundsOut)
      this.WithdrawMAX(fundsOut).then(()=>{
        this.router.navigate(['/home/portafolio']);       
      })
      .catch(()=>{
        this.button_disabled = false;
        this.validating = false;
  
      })
    }else{
      this.Withdraw(fundsOut).then(()=>{
        // console.log(response);
        this.router.navigate(['/home/portafolio']);       
      })
      .catch(()=>{
        this.button_disabled = false;
        this.validating = false;
  
      })
    }
    // console.log(fundsOut)
    
    
  }

  async PaymentProccessUsd(coin:any, qty:any){
    this.validating = true;
    this.button_disabled = true;

    const fundsOut:WithdrawModel = {
      portfolio_name: this.portfolio_name,
      withdraw_to: "My Wallet at another custodian",
      instrument: coin,
      qty: qty,
      notes: "Wired from Chase"
    }

    // console.log(fundsOut)
    this.Withdraw(fundsOut).then((response:any)=>{
      // console.log(response);
      this.router.navigate(['/home/portafolio']);       
    })
    .catch(()=>{
      this.button_disabled = false;
      this.validating = false;

    })
    
  }

  //****************************************************
  //****************************************************


  ButtonValidate(){

    this.validating = true;

    const validate:any = {
      coin: this.crypto_name,
      address2validate: this.address_data
    }

    this.ValidateAddress(validate).then((data:any)=>{
      if(data.isValid){
        this.toastr.success("Valid Wallet to "+this.crypto_name);
        this.usd_select = false;
        this.crypto_select = false;
        this.payment_method_select = false;
        this.withdraw = false;
        this.button_disabled = false;
        this.crypto_valid_value = false;
        this.label_show = false;
        this.isChecked = false;
        this.button_address = false;
        this.final_dialog = true;
        this.validating = false;
      }else{
        this.validating = false;
      }
    })
    .catch(()=>this.validating = false);
    
  }

  InputAddress(value:any){
    if(value != ''){
      this.address_data = value;
      this.button_address = true;
    }else{
      this.address_data = ''
      this.button_address = false;
    }
  }

  InputCurrency(value:any){
    this.validating = true;
    clearTimeout(this.timerConversionCurrency);
    this.timerConversionCurrency = setTimeout(async ()=>{
      const fx_convert:ConvertModel = {
        from_curr: this.client_data.currency,
        to_curr: "USD",
        amount: Number(value)
      }
      let currency_value = await this.Convert(fx_convert);
      this.input_usd_value = Number(value * currency_value.price).toFixed(3);
      this.validating = false;
    },250)

  }

  InputUsd(value:any){
    this.validating = true;
    clearTimeout(this.timerConversionUsd);
    this.timerConversionUsd = setTimeout(async ()=>{
      const fx_convert:ConvertModel = {
        from_curr: "USD",
        to_curr: this.client_data.currency,
        amount: Number(value)
      }
      let currency_value = await this.Convert(fx_convert);
      this.input_currency_value = Number(value * currency_value.price).toFixed(3);
      this.validating = false;
    },250)
  }

  InputCrypto(value:any){
    this.withdrawMAX = false;
    this.crypto_qty = Number(value);
    if(value != '' && value != 0 && value <= this.total_qty_crypto){
      this.crypto_valid_value = true;
    }else{
      this.crypto_valid_value = false;
    }

  }


  //SERVICES =============================================================================================================================
  
  //Withdraw Test Api *************************************************************
  
  Withdraw(data:WithdrawModel): Promise<WithdrawModelResponse>{
    return new Promise<WithdrawModelResponse>((resolve, reject) => {
      this.subscription.add(this.test_payments.TestWithdraw(data).subscribe(data=>{
        resolve(data);
      },err=>{
        console.log("Error Withdraw:", err);
        reject(null);
        this.validating = false;
      }));
    });
  }

  WithdrawMAX(data:WithdrawMaxModel): Promise<WithdrawModelResponse>{
    return new Promise<WithdrawModelResponse>((resolve, reject) => {
      this.subscription.add(this.assets.WithdrawMax(data).subscribe(data=>{
        resolve(data);
      },err=>{
        console.log("Error Withdraw:", err);
        reject(null);
        this.validating = false;
      }));
    });
  }

  Convert(data:ConvertModel): Promise<ConvertDataModelResponse>{
    return new Promise<ConvertDataModelResponse>((resolve, reject) => {
      this.subscription.add(this.market.Convert(data).subscribe(response=>{
        resolve(JSON.parse(response.payload));
      },err=>{
        console.log("Error Convert:",err)
        resolve(null);
      }))
    })

  }


  ValidateAddress(data:any): Promise<any>{
    return new Promise((resolve, reject) => {
      this.subscription.add(this.wallet_service.ValidateWalletAddress(data).subscribe((data:any)=>{
        resolve(data);
      },err=>{
        console.log("Error ValidateAddress:", err);
        resolve(null);
      }));
    });
  }

  GetEstimatedFee(data:GetGasFeeEstimateModel): Promise<GetGasFeeEstimateModelResponse>{
    return new Promise<GetGasFeeEstimateModelResponse>((resolve, reject) => {
      this.subscription.add(this.assets.GetGasFeeEstimate(data).subscribe((data:any)=>{
        resolve(data);
      },err=>{
        console.log("Error GetEstimatedFee:", err);
        resolve(null);
      }));
    });
  }

  GetBalanceForSymbol(data:BalanceForSymbolModel): Promise<BalanceForSymbolModelResponse>{
    return new Promise((resolve, reject) => {
      this.subscription.add(this.assets.GetPortafolioBalancesForSymbol(data).subscribe((balance:BalanceForSymbolModelResponse)=>{
        resolve(balance);
      },err=>{
        console.log("Error GetBalanceForSymbol:", err);
        resolve(null);
      }));
    });
  }


  GetClientConfig(): Promise<ClientConfigResponse>{
    return new Promise((resolve, reject) => {
      this.subscription.add(this.client.GetClientConfig().subscribe((data:ClientConfigResponse)=>{
        resolve(data);
      },err=>{
        console.log("Error GetClientConfig:", err);
        resolve(null);
      }));   
    });
  }
  

  GetDefaultPortafolio(): Promise<PortafolioDefault>{
    return new Promise<PortafolioDefault>((resolve, reject) => {
      this.subscription.add(this.assets.GetDefault().subscribe(response=>{
        resolve(response);
      },err=>{
        console.log("Error GetDefault:", err);
        resolve(null);
      }));
    });
  }

  GetAllPaymentMethods(): Promise<clientPayMethod[]>{
    const data:any = {
      direction: "out"
    }
    return new Promise((resolve, reject) => {
      this.subscription.add(this.payments.GetClientPaymentMethods(data).subscribe(response=>{
        resolve(response);
      },err=>{
        console.log("Error GetAllPaymentMethods:", err);
        resolve(null);
      }));
    });
  }

  GetPortfolioBalances(): Promise<BalanceModelResponse[]>{
    const portfolio:BalanceModel ={
      portfolio_name: this.portfolio_name
    }
    return new Promise<BalanceModelResponse[]>((resolve, reject) => {
      this.subscription.add(this.assets.GetPortafolioBalances(portfolio).subscribe(response=>{
        resolve(response);
      },err=>{
        console.log("Error GetAllPaymentMethods:", err);
        resolve(null);
      }));
    });
  }

  ShowError(msg:string, title:string = "", disableTimeOut: boolean = true){
    console.error(msg);
    this.toastr.error(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,        
      positionClass: 'toast-center-center',        
    });
  }
  

  ShowWarning(msg:string, title:string = "", disableTimeOut: boolean = true){
    console.warn(msg);
    this.toastr.warning(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,        
      positionClass: 'toast-center-center',        
    });
  }

}