import { Injectable } from '@angular/core';
import * as echarts from 'echarts';

@Injectable({
  providedIn: 'root'
})
export class GraphPortafolioService {

  constructor() { }

  public makeOption() {
    var languageCountryCode;

    return {
      animation:true,
      grid: {
        left: '4%',
        right: '0%',
        height: '80%',
        width:'94%',        
        bottom: 60,
        top: 5,
      },
      backgroundColor: 'rgb(0, 0, 0)',
      legend: {
        show: true,
        itemWidth: 18 * 1.5,
        itemHeight: 10 * 1.5,
        height: 80,
        bottom: 0,      // TODO: dynamically adjust based on device type //22
        padding: 8,
        inactiveColor: 'rgba(100, 100, 100, 0.50)',
        data: [
          { name: 'Total Worth', icon: 'image://assets/Graph/totalWorth.png', },
          { name: 'Unrealized P/L', icon: 'image://assets/Graph/unrealizedPnL.png' },
          { name: 'Realized P/L', icon: 'image://assets/Graph/realPAndL.png' },
        ],
      },

      xAxis: [
        {
          type: 'time',
          min: 'dataMin',
          max: 'dataMax',
          splitNumber: 2,
          interval: 2,
          scale: true,
          axisPointer: {
            snap: true,
            lineStyle: {
              color: '#7581BD',
              width: 1
            },
            label: {
              show: false,
              formatter: function (params) {
                return echarts.format.formatTime('MM-dd hh:mm', params.value);
              },
              backgroundColor: '#7581BD'
            },
          },
          splitLine: {
            show: false
          },
          axisLabel: {
            formatter: function (value) {
              return echarts.format.formatTime('MM-dd hh:mm', value);
            },
            textStyle: {
              fontSize: 10,
              color:"",
            },
            showMinLabel: false,
            showMaxLabel: false,
            margin: 12,
          },
          axisLine: {
            onZero: false,
            show: true,
            lineStyle: {
              color: '#FFFFFF'
            }
          },
        },
      ],
      yAxis: [
        {
          show: true,
          scale: true,
          type: 'value',
          splitNumber: 3,
          axisTick: {
            show: false
          },
          splitLine: {
            show: false,
            lineStyle: {
              type: 'dotted',
              opacity: 0.65,
            },
          },
          position: 'left',
          axisLabel: {
            textStyle: {
              fontSize: 10,
              color:"",

            },
            inside: true,
            verticalAlign: 'middle',
            margin: 0,
            showMinLabel: false,
            showMaxLabel: false,
            formatter: function (value) {
              const options = {};
              const numberFormat = new Intl.NumberFormat(languageCountryCode, options);
              var formattedValue = numberFormat.format(value);
              return formattedValue;
            },
          },
          min: function (value) {
            var min = value.min;
            return Math.round((min + Number.EPSILON) * 100) / 100;
          },
          max: function (value) {
            var max = value.max;
            return Math.round((max + Number.EPSILON) * 100) / 100;
          },
        },
        {
          show: true,
          type: 'value',
          scale: true,
          splitLine: {
            show: false,
            lineStyle: {
              type: 'dotted',
              opacity: 0.75,
            },
          },
          axisTick: {
            show: false
          },
          position: 'right',
          axisLabel: {
            textStyle: {
              fontSize: 10,
            },
            inside: false,
            verticalAlign: 'middle',
            align: 'right',
            margin: 0,
            showMinLabel: false,
            showMaxLabel: false,
            formatter: function (value) {
              const options = {};
              const numberFormat = new Intl.NumberFormat(languageCountryCode, options);
              var formattedValue = numberFormat.format(value);
              return formattedValue;
            },
          },

          min: function (value) {
            var multFactor = 1 / 1.026;
            var min = value.min;
            if (value.min < 0) {
              multFactor = 1 / multFactor;
            }
            min = min * multFactor;
            var newMin = Math.round((min + Number.EPSILON) * 100) / 100;
            return newMin;
          },
          max: function (value) {
            var multFactor = 1.08;
            if (value.max < 0) {
              multFactor = 1 / multFactor;
            }
            var max = value.max * multFactor;
            var newMax = Math.round((max + Number.EPSILON) * 100) / 100;
            return newMax;
          },

        },

      ],

      visualMap: [
        {
          show: false,
          dimension: 1,
          seriesIndex: 1,
          pieces: [
            {
              lt: 0,
              color: 'red'
            },
            {
              gte: 0,
              lte: 1000000,
              color: 'green'
            }
          ]
        },
        {
          show: false,
          dimension: 1,
          seriesIndex: 2,
          pieces: [
            {
              lt: 0,
              color: 'rgb(255, 0, 0)',
            },
            {
              gte: 1,
              lte: 1000000,
              color: 'rgb(0, 100, 0)',
            }
          ]
        },

      ],
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: "line",
          animation: false
        },
        formatter: function(param) {
          return param;
        },
        borderColor: 'rgba(0, 0, 0, 1.0)',
        backgroundColor: 'rgba(32, 32, 32, 0.85)',
        textStyle: {
          color: 'rgba(255, 255, 255, 1.0)',
          fontSize: 10,
        },
        position: function (pos, params, el, elRect, size) {
          var obj = { top: 10 };
          obj[['left', 'right'][+(pos[0] < size.viewSize[0] / 2)]] = 0;
          return obj;
        },
        extraCssText: 'width: 12rem'

      },
      series: [
        {
          type: 'line',
          yAxisIndex: 0,
          name: 'Total Worth',
          smooth: true,
          data: [],
          axisLabel: {
            textStyle: {
              fontSize: 12
            },
          },
          itemStyle: { color: 'rgb(255, 255, 0)', },
          lineStyle: {
            color: 'rgb(255, 255, 0)',
            width: 1,
          }
        },
        {
          type: 'line',
          yAxisIndex: 1,
          smooth: true,
          name: 'Unrealized P/L',
          data: [],
          axisLabel: {
            textStyle: {
              fontSize: 12
            },
          },
          lineStyle: {
            width: 1,
          },
          itemStyle: {
          },
        },
        {
          type: 'bar',
          yAxisIndex: 1,
          barWidth: 8,
          name: 'Realized P/L',
          z: 4,
          data: [],
          axisLabel: {
            textStyle: {
              fontSize: 12
            },
          },
          markPoint: {},
        },
      ],
    }

  }

}
