import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { ClientConfigResponse } from 'src/models/Client/ClientConfig.model';
import { clientPayMethod } from 'src/models/PaymentMethods/clientPayMethods.model';
import { PortafolioDefault } from 'src/models/Portafolio/portfolioDefault.model';
import { AllProducts } from 'src/models/Portafolio/portfolioProducts.model';
import { ClientService } from 'src/services/Client/client.service';
import { ManageDataService } from 'src/services/manage-data.service';
import { PaymentService } from 'src/services/payments/payment.service';
import { PortafolioService } from 'src/services/portafolio/portafolio.service';
import { TestFoundsService } from 'src/services/TestFounds/test-founds.service';
import { confirm } from 'src/models/Confirm/confirm.model';
import { EnumConfirmation } from 'src/models/Enum/EnumAuthentication.enum';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';
import { ConfirmationComponent } from 'src/app/PopUp/confirmation/confirmation.component';
import { DepositModel, DepositResponseModel } from 'src/models/TestFounds/testFounds.model';
import { WalletService } from 'src/services/Wallet/wallet.service';
import { GetWalletAddressDepositModel, GetWalletAddressDepositModelResponse } from 'src/models/Wallet/wallet.model';
import { PaymentMethodsComponent } from 'src/app/PaymentMethods/payment-methods/payment-methods.component';
import { MarketService } from 'src/services/Market/market.service';
import { ConvertDataModelResponse, ConvertModel, ConvertModelResponse } from 'src/models/Market/market.model';


@Component({
  selector: 'app-deposit',
  templateUrl: './deposit.component.html',
  styleUrls: ['./deposit.component.scss']
})
export class DepositComponent implements OnInit, OnDestroy {

  language: string;
  langs: string[] = [];

  assets_data = [];
  deposit_methods = [];

  usd_select:any = false;
  crypto_select:any = false;
  payment_method_select:any = false;
  deposit:any = true;
  final_dialog:any = false;
  validating:any = false;

  payment_method_name:any;
  crypto_qty:any;
  crypto_name:any;

  client_data:any;
  input_currency_value:any = 0;
  input_usd_value:any = 0;

  loadCompleteOK:boolean = false;
  clientPortafolio:any;

  disable_deposit:boolean = false;

  QRCode:string = "data:image/png;base64,";
  timerConversionCurrency:any;
  timerConversionUsd:any;



  public subscription: Subscription = new Subscription();

  constructor( private dialog: MatDialog,private translate: TranslateService,private manage_data: ManageDataService<any>, private toastr:ToastrService, 
    private test_payments:TestFoundsService, private client:ClientService, private market:MarketService,
    private payments:PaymentService, private assets:PortafolioService, 
    private router:Router, private spinner:NgxSpinnerService, private wallet:WalletService
    ) {
   }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  
  async ngOnInit() {
    this.spinner.show();
    await this.InitDeposit();
    await this.IsValidatedID();
    this.spinner.hide();  
    }
  
  //************************************************************************************
  //Confirm Validate ID
  //************************************************************************************


  async ConfirmValidateID(): Promise<void> {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "20rem";
    dialogConfig.width = "40rem";

    var data = {
      title: await this.translate.get("Errores.mustValidateID").toPromise(),
      icon: 'quest',
      conten: await this.translate.get("Errores.wantValidateID").toPromise()
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(), data);
    let dialogRef = this.dialog.open(ConfirmationComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.router.navigate(['/vouched']);
      }

    }));
  }

  //************************************************************************************
  //Is Validated ID?
  //************************************************************************************

  async IsValidatedID(){
    let data = await this.GetClientConfig()
    if(data.id_verified){
      this.disable_deposit = false;
    }else{
      this.disable_deposit = true;
      this.ConfirmValidateID();
    }
  }

  //************************************************************************************
  //Init Deposit
  //************************************************************************************

  async InitDeposit(){
    try {
      let getClientConfigResponse = await this.GetClientConfig();
      if (!getClientConfigResponse) {
        this.ShowError("Error to get client config");
        return;
      }

      // console.log("getClientConfigResponse:", getClientConfigResponse);

      this.client_data = getClientConfigResponse;
      

      let getAllProductResponse = await this.GetAllProduct();
      if (!getAllProductResponse) {
        this.ShowError("Error to get all products");
        return;
      }

      this.assets_data = getAllProductResponse.filter(element => element.trading_disabled == false && element.fx_stablecoin == false && element.quote_currency == 'USD');

      // console.log("assets_data:", this.assets_data);

      let getAllPaymentMethodsResponse = await this.GetAllPaymentMethods();
      if (!getAllPaymentMethodsResponse) {
        this.ShowWarning("Please add a payment method to add money to the portfolio", "There are no payment methods", false);
      }

      // console.log("getAllPaymentMethodsResponse:", getAllPaymentMethodsResponse);
      this.deposit_methods = getAllPaymentMethodsResponse;

      let getDefaultPortafolioResponse = await this.GetDefaultPortafolio();
      if (!getDefaultPortafolioResponse) {
        this.ShowError("Error to get client portafolio");
        return;
      }

      // console.log("getDefaultPortafolioResponse:", getDefaultPortafolioResponse);
      this.clientPortafolio = getDefaultPortafolioResponse;

      this.loadCompleteOK = true;

    } catch (error) {
      console.error("Error on InitDeposit:", error);
      
    }
  }

  SelectCurrency(value:any){
    switch (value) {
      case 'USD':
        this.crypto_select = false;
        this.usd_select = true;
        if(this.deposit_methods.toString() === ""){
          this.ConfirmAddMethod();
          break;
        }

        break;
      
      case 'Select':
      
        break;
    
      default:
        this.usd_select = false;
        this.crypto_select = true;
        this.payment_method_select = false;
        this.crypto_name = value;
        this.crypto_qty = 100;
        break;
    }
  }

 async ConfirmAddMethod(): Promise<void> {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.disableClose = true;
    dialogConfig.width = "40rem";
    var data = {
      title: await this.translate.get('dialog.generateDeposit').toPromise(),
      icon: 'quest',
      conten: await this.translate.get('dialog.agreepaymethod').toPromise()
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(), data);
    let dialogRef = this.dialog.open(ConfirmationComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.AddPaymentMethods()
      }
    }));
  }

  SelectMethod(value:any){
    switch (value) {      
      case 'Select':
        this.payment_method_select = false;
        break;
    
      default:
        this.payment_method_select = true;
        this.payment_method_name = value;
        break;
    }
  }

  ContinuePaymentButton(){
    this.usd_select = false;
    this.crypto_select = false;
    this.payment_method_select = false;
    this.deposit = false;
    this.final_dialog = true;
  

  }

  //****************************************************
  //Deposit Without Payment Method Saved
  //****************************************************

  AddPaymentMethods(){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "100rem";
    const data = {
      action: 'Deposit',
      direction: null
    }
    this.manage_data.emitEvent(EnumEventsPopUp.ActionPaymentMethods.toString(),data)
    let dialog = this.dialog.open(PaymentMethodsComponent,dialogConfig);
    this.subscription.add(dialog.afterClosed().subscribe(async ()=>{
      
      let getAllPaymentMethodsResponse = await this.GetAllPaymentMethods();
      if (!getAllPaymentMethodsResponse) {
        this.ShowWarning("Please add a payment method to add money to the portfolio", "There are no payment methods", false);
      }
      this.deposit_methods = getAllPaymentMethodsResponse;
    }))
  }
  //****************************************************
  //Generate Wallet QR 
  //****************************************************
  async GenerateQR(){
    const Coin:GetWalletAddressDepositModel = {
      coin: this.crypto_name
    }

    let responseQR = await this.GetInboundWallet(Coin);
    if (!responseQR) {
      this.ShowError("An error has occurred, please try again", "Error");
      return;
    }

    this.QRCode += responseQR.qr_code;

  }

  //****************************************************
  //Payment Process
  //****************************************************

  async PaymentProccessCrypto(){
    this.validating = true;

    if (this.loadCompleteOK === false) {
      this.ShowError("An error has occurred, please try to reload the page", "Error");
      return;
    }

    const FundsIn:DepositModel = {
      portfolio_name: this.clientPortafolio.name,
      instrument: this.crypto_name,
      qty: 100,
      cost_basis: 1,
      from: "Chase-8074",
      notes: "Wired from Chase"
    }


    let depositResponse = await this.Deposit(FundsIn);
    if (!depositResponse) {
      this.ShowError("An error has occurred, please try again", "Error");
      this.validating = false;
      return;
    }
    //console.log("depositResponse:", depositResponse);
    this.router.navigate(['/home/portafolio']);       


  }

  async PaymentProccessUsd(coin:any, qty:any){
    this.validating = true;

    if (this.loadCompleteOK === false) {
      this.ShowError("An error has occurred, please try to reload the page", "Error");
      return;
    }

    const FundsIn:DepositModel = {
      portfolio_name: this.clientPortafolio.name,
      instrument: coin,
      qty: Number(qty),
      cost_basis: 1,
      from: "Chase-8074",
      notes: "Wired from Chase"
    }

    let depositResponse = await this.Deposit(FundsIn);
    if (!depositResponse) {
      this.validating = false;
      return;
    }
    //console.log("depositResponse:", depositResponse);
    this.router.navigate(['/home/portafolio']);       

  }

  //****************************************************
  //****************************************************

  InputCurrency(value:any){
    this.validating = true;
    clearTimeout(this.timerConversionCurrency);
    this.timerConversionCurrency = setTimeout(async ()=>{
      const fx_convert:ConvertModel = {
        from_curr: this.client_data.currency,
        to_curr: "USD",
        amount: Number(value)
      }
      let currency_value = await this.Convert(fx_convert);
      this.input_usd_value = Number(value * currency_value.price).toFixed(3);
      this.validating = false;
    },250)

  }

  InputUsd(value:any){
    this.validating = true;
    clearTimeout(this.timerConversionUsd);
    this.timerConversionUsd = setTimeout(async ()=>{
      const fx_convert:ConvertModel = {
        from_curr: "USD",
        to_curr: this.client_data.currency,
        amount: Number(value)
      }
      let currency_value = await this.Convert(fx_convert);
      this.input_currency_value = Number(value * currency_value.price).toFixed(3);
      this.validating = false;
    },250)
  }

  //SERVICES =============================================================================================================================
  
  //Deposit Test Api *************************************************************

  Deposit(data:DepositModel): Promise<DepositResponseModel>{
    return new Promise<DepositResponseModel>((resolve, reject) => {
      this.subscription.add(this.test_payments.TestDeposit(data).subscribe(data=>{
        resolve(data);
      },err=>{
        console.log("Error Deposit:", err);
        resolve(null);
      }));
    });
  }

  Convert(data:ConvertModel): Promise<ConvertDataModelResponse>{
    return new Promise<ConvertDataModelResponse>((resolve, reject) => {
      this.subscription.add(this.market.Convert(data).subscribe(response=>{
        resolve(JSON.parse(response.payload));
      },err=>{
        console.log("Error Convert:",err)
        resolve(null);
      }))
    })

  }


  GetClientConfig(): Promise<ClientConfigResponse>{
    return new Promise((resolve, reject) => {
      this.subscription.add(this.client.GetClientConfig().subscribe((data:ClientConfigResponse)=>{
        resolve(data);
      },err=>{
        console.log("Error GetClientConfig:", err);
        resolve(null);
      }));   
    });
  }


  GetAllProduct(): Promise<AllProducts[]>{
    return new Promise<AllProducts[]>((resolve, reject) => {
      this.subscription.add(this.assets.GetAllProducts().subscribe(response=>{
        resolve(response);
      },err=>{
        console.log("Error GetAllProduct:", err);
        resolve(null);
      }));
    });
  }

  GetDefaultPortafolio(): Promise<PortafolioDefault>{
    return new Promise<PortafolioDefault>((resolve, reject) => {
      this.subscription.add(this.assets.GetDefault().subscribe(response=>{
        resolve(response);
      },err=>{
        console.log("Error GetDefault:", err);
        resolve(null);
      }));
    });
  }

  GetAllPaymentMethods(): Promise<clientPayMethod[]>{
    const data:any = {
      direction: "in"
    }
    return new Promise((resolve, reject) => {
      this.subscription.add(this.payments.GetClientPaymentMethods(data).subscribe(response=>{
        resolve(response);
      },err=>{
        console.log("Error GetAllPaymentMethods:", err);
        resolve(null);
      }));
    });
  }

  GetInboundWallet(data:GetWalletAddressDepositModel): Promise<GetWalletAddressDepositModelResponse>{
    return new Promise<GetWalletAddressDepositModelResponse>((resolve, reject) => {
      this.subscription.add(this.wallet.GetWalletForDeposit(data).subscribe(response=>{
        resolve(response);
      },err=>{
        console.log("Error GetInboundWallet:", err);
        resolve(null);
      }))
    })
  }

  ShowError(msg:string, title:string = "", disableTimeOut: boolean = true){
    // console.error(msg);
    this.toastr.error(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,        
      positionClass: 'toast-center-center',        
    });
  }

  ShowWarning(msg:string, title:string = "", disableTimeOut: boolean = true){
    // console.warn(msg);
    this.toastr.warning(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,        
      positionClass: 'toast-center-center',        
    });
  }

}
