import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map, Observable } from 'rxjs';
import { GetWalletAddressDepositModel, GetWalletAddressDepositModelResponse, ValidateWalletAddressModel, ValidateWalletAddressModelResponse } from 'src/models/Wallet/wallet.model';
import { AppSettingsService } from '../AppSettingsInit/app-settings.service';

@Injectable({
  providedIn: 'root'
})
export class WalletService {
  ApiBase:string
  ApiValidateWalletAddress:string
  ApiGetWalletForDeposit:string;
  ApiWithdrawMaxForCoin:string;

  headers = new HttpHeaders({
    'Accept': 'application/json; odata=verbose',
    'Content-Type': 'application/json; charset=utf-8',
    'Access-Control-Allow-Origin':'*',
    'Access-Control-Allow-Headers': 'X-API-KEY, Origin, X-Requested-With, Content-Type, Accept, Access-Control-Request-Method',
    'Access-Control-Allow-Methods': 'GET, POST, OPTIONS, PUT, DELETE',
    'Allow': 'GET, POST, OPTIONS, PUT, DELETE'
    });
    options = { headers: this.headers };
  
  constructor(private http:HttpClient, private settingsService: AppSettingsService) {
    this.ApiBase = this.settingsService.getapidatos().APIS.ApiBase;
    this.ApiValidateWalletAddress = this.settingsService.getapidatos().APIS.ApiValidateWalletAddress;
    this.ApiGetWalletForDeposit = this.settingsService.getapidatos().APIS.ApiGetWalletForDeposit;
    this.ApiWithdrawMaxForCoin = settingsService.getapidatos().APIS.ApiWithdrawMaxForCoin;

  }

  ValidateWalletAddress(data:ValidateWalletAddressModel): Observable<ValidateWalletAddressModelResponse>{
    return this.http.post<ValidateWalletAddressModelResponse>(this.ApiBase+this.ApiValidateWalletAddress,data,this.options).pipe(
      map((res: ValidateWalletAddressModelResponse) => {
        return res;
      })
    );
  }

  GetWalletForDeposit(data:GetWalletAddressDepositModel): Observable<GetWalletAddressDepositModelResponse>{
    return this.http.post<GetWalletAddressDepositModelResponse>(this.ApiBase+this.ApiGetWalletForDeposit,data,this.options).pipe(
      map((res: GetWalletAddressDepositModelResponse) => {
        return res;
      })
    );
  }

  WithdrawMaxForCoin(data:any): Observable<any>{
    return this.http.post<any>(this.ApiBase+this.ApiWithdrawMaxForCoin,data,this.options).pipe(
      map((res: any) => {
        return res;
      })
    );
  }
}
